*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Nunito sans',sans-serif;
  line-height: 1.15;
  background-image: linear-gradient(45deg,#02aab0, #00cdac);
  color: #212529;
  height: 100vh;
}

header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 1em 2em;
}

header input{
  border: none;
  padding: 0.7em 1.5em;
  width: 75%;
  border-radius: 0.3em;
  color: white;
  box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
  background: inherit;
  font-weight: bold;
}

::placeholder{
  color: #fff;
}



header button{
  padding: 0.7em 3em;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  font-weight: bold;
  transition: 250ms;
}

header button:hover {
  transform: scale(1.1);
}

header button:focus, header input:focus{
  outline: 2px solid #fff;
  outline-offset: 3px;
}


main{
  width: 100%;
  height: 100%;
}

.items{
  width: 100%;
  padding: 1.5em 1em;
}

.col{
  border-radius: 0.5em;
  padding: 0.4em 1.5em;
  max-width: 360px;
  color: #fff;
  margin: 0 auto;
  width: 100%;
  box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
  background: inherit;
}
.col h3, .col h1{
text-transform: uppercase;
}

.col h1{
  text-align: center;
  margin: 10px 0;
  font-size: 2.5em;
}

.col h3{
  margin-bottom: 5px;
}

.col div{
  margin-bottom: 20px;
}


.col div ul{
  margin: 8px 0 0 40px;
}

.flex{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flex h1{
  font-size: 7em;
}

.flex p{
  font-size: 1.5em;
  text-align: center;
}

@media (max-width: 600px) {
  header{
    flex-direction: column;
  }

  header input, header button{
    width: 100%;
    margin-bottom: 1em;
  }
}